.hero-image {
  height: 100vh;
  background-image: url('/images/ravenel_arial.jpg');
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  position: absolute;
  left: 0;
  right: 0;
  opacity: 0.6;
}

.hero-background {
  height: 100vh;
  background-color: #006494;
}

.hero-font {
  position: absolute;
  z-index: 1;
  font-size: 110px;
  color: white;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  opacity: 1;
  -webkit-animation: slowfade 1.5s;
  -moz-animation: slowfade 1.5s;
  -ms-animation: slowfade 1.5s;
  -o-animation: slowfade 1.5s;
}

.info-section {
  background-color: #006494;
}

.primary-container {
  padding-top: 128px;
  padding-bottom: 128px;
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
}

.title-about {
  text-align: center;
  letter-spacing: 2px;
  padding: 30px;
  font-size: 36px;
  color: #e8f1f2;
}
.title-tools {
  text-align: center;
  letter-spacing: 2px;
  padding: 30px;
  font-size: 36px;
  color: #13293d;
}

.about-paragraph {
  text-align: center;
  letter-spacing: 1px;
  color: #e8f1f2;
  opacity: 0.5;
  margin: 24px;
  font-size: 20px;
  max-width: 50%;
}

.divider {
  margin-bottom: 30px;
  width: 75px;
  height: 5px;
  border: none;
}

.btn-align {
  padding: 50px;
}

.btn {
  text-align: center;
  font-weight: bold;
  padding: 18px 32px;
  border-radius: 300px;
  border: none;
  font-size: 20px;
}

.box-t {
  height: 160px;
  width: 160px;
  background: #e8f1f2;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  /* margin-bottom: 30px;
  margin-left: 90px;
  margin-right: 90px; */
}

.row {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}

.column {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.box-p {
  height: 400px;
  width: 480px;
  background-color: #13293d;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
  margin: 2rem;
}
.box-d {
