.drafty-shot {
  /* display: flex; */

  border-style: solid;
  border-width: 1;
  border-color: grey;
  -webkit-box-shadow: 2px 2px 8px 0px rgba(0, 0, 0, 0.2);
          box-shadow: 2px 2px 8px 0px rgba(0, 0, 0, 0.2);
}

@keyframes slowfade {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

/* Firefox < 16 */

/* Safari Chrome and Opera > 12.1 */
@-webkit-keyframes slowfade {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

/* Internet Explorer */

/* Opera < 12.1 */

.fixed-top {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  z-index: 1030;
}



